.square {
	width: 100%;
	height: 100%;
}

/*鼠标悬停时的文字样式*/
.squaretext{	
	display: flex;
	justify-content: center;
	align-items:center;
	line-height: 130px;
	color: #000000;
	font-size:16px;
	font-weight: normal;
}

.squaretext_nohover {
	display: flex;
    justify-content: center;
    align-items: center;
    line-height: 130px;
    color: white;
    font-size: 16px;
    font-weight: normal;
}